import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import "./Home.css";
import Header from "../components/Header";

function Home() {
  const history = useHistory();

  useEffect(() => {
    const hasVisited = localStorage.getItem("hasVisitedLuvlyFancy");
    if (!hasVisited) history.push("/");
  }, []);

  return (
    <div className="Home">
      <div className="home-zone">
        <div className="home-pic-1">
          <div id="home-pic-1-gradient"><Header/></div>
        </div>
      </div>
    </div>
  );
}

export default Home;
