import React from 'react';
import './Navbar.css'
import { useHistory } from "react-router-dom";

function Navbar() {
  const history = useHistory();
  return (
    <div className='Navbar'>
      <button className='nav-buttton' onClick={() => history.push("/shop")}>Shop</button>
      <button className='nav-buttton' onClick={() => history.push("/shows")}>Shows</button>
      <button className='nav-buttton' onClick={() => history.push("/fancytopia")}>Into Fancytopia</button>
    </div>

  )
}

export default Navbar
