import React, { useEffect } from "react";
import "./NotFound404.css";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";

function NotFound404() {
  const history = useHistory();

  useEffect(() => {
    const hasVisited = localStorage.getItem("hasVisitedLuvlyFancy");
    if (!hasVisited) history.push("/");
  }, []);

  return (
    <div className="NotFound404">
      <div className="notFound404-zone">
        <div className="notFound404-pic-1"></div>
        <div className="notFound404-txt">
          <span id="notFound404-txt-1">  </span>
        </div>
        <div className="notFound404-button-zone">
          <button
            className="notFound404-button"
            onClick={() => {
              localStorage.setItem("hasVisitedLuvlyFancy", "true");
              let baseUrl = window.location.origin + "/luvlyfancy";
              window.location.href = baseUrl;
            }}
          >
            
            <span>BACK</span>
          </button>
        </div>
      </div>
    </div>
  );
}

export default NotFound404;
